<template>
  <div class="connectWallet">
    <button @click="showWallet" v-show="account.length>1" class="rainbow-btn button-connect">
      {{ account.substr(0, 6) + '...' + account.substr(39, 3) }}
    </button>
    <button size="mini" @click="showWallet" v-show="account.length<1" class="rainbow-btn button-connect">
      connect
    </button>
    <div v-show="isShowConnectStatus" class="connect-panel " @click="isShowConnectStatus=false;$emit('changeState')">
      <div class="mask"></div>
      <div class="connect-status-box animate__animated  animate__backInDown" @click.stop>
        Connected with {{ connectArr[connectIdx] }}
        <div class="connect-status" @click="disConnect" v-show="connectIdx!=1">
          unConnect
        </div>
      </div>
    </div>

    <div class="connect-panel" v-show="isShowConnect" @click="isShowConnect=false;$emit('changeState')">
      <div class="mask"></div>
      <div class="content  animate__animated  animate__backInDown" @click.stop>
        <div class="item" @click="connectWallet(1)">
          <div class="img"></div>
          <div class="name">MetaMask</div>
          <div class="info">Connect to your MetaMask Wallet</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import getWeb3 from "@/utils/getWeb3";
export default {
  name: "ConnectWallet",
  props:['changeState','getWeb3'],
  data() {
    return {
      account:"",
      connectIdx: 0,
      connectArr: ['', 'MetaMask', 'WalletConnect', 'Fortmatic'],
      isShowConnectStatus: false,
      isLoading: false,
      isShowConnect: false
    }
  },
  computed: {

  },
  created() {
    if(localStorage.getItem('wallet')=='mateMask'){
      this.connectWallet(1)
    }
  },
  methods: {
    loginOut() {
      this.$store.dispatch('app/loginOutWeb3')
    },
    async connectWallet(idx) {
      this.connectIdx = idx
      this.isLoading = true
      //
      // window.ethereum.request({
      //   method: 'wallet_addEthereumChain',
      //   params: [{
      //     chainId: "0x29a",
      //     chainName: "Rainbow",
      //     rpcUrls: [
      //       "https://rpcapi.rainbow.kim",
      //     ],
      //     iconUrls: [
      //       "https://uni-rb.oss-ap-southeast-1.aliyuncs.com/images/rblogo.png"
      //     ],
      //     blockExplorerUrls: [
      //       "https://rainbow.kim"
      //     ],
      //     nativeCurrency: {
      //       name: "RBS",
      //       symbol: "RBS",
      //       decimals: 18
      //     }
      //   }]
      // })
      if (idx == 1) {
        localStorage.setItem('wallet', 'mateMask')
        if (typeof window.ethereum == 'undefined') {
          alert("please download metamask")
        }
        await window.ethereum.enable()
        window.ethereum.on('accountsChanged', (accounts) => {
          // Handle the new accounts, or lack thereof.
          // "accounts" will always be an array, but it can be empty.

        });
        let _this = this
        getWeb3().then((res) => {
          this.isShowConnect = false
          _this.account= res.account
          this.$emit('getWeb3',res)
        })
        this.isShowConnect = false
        this.$emit('changeState')
      }
    },
    showWallet() {
      this.$emit('changeState',true)
      if (this.isConnected) {
        this.isShowConnectStatus = true
      } else {
        this.isShowConnect = true
      }
    },
    disConnect() {
      if (this.connectIdx != 1) {
        if (this.provider) this.provider.disconnect()
        this.isShowConnectStatus = false
        this.connectIdx = 0
      }
      this.$emit('changeState')
    }
  },
}
</script>

<style lang="scss" scoped>
.connectWallet {
  color: #2c3e50;
  display: flex;

  .button-connect {
    padding: 0 10px;
    width: 130px;
    cursor: pointer;
    line-height: 0;
    height: 30px;
    background: linear-gradient(90deg,#12c2e9, #c471ed 53%, #f64f59);
    border-radius: 10px;
    color: white;
    border: none;
    box-shadow: 0px 3px 6px 0px rgba(128, 4, 149, 0.30);
  }
  .button-connect:active{
    border: none;
  }
  .connect {
    height: 200px;
    margin-top: 25%;
  }

  .connect-panel {
    z-index: 1000;
    text-align: center;
    position: fixed;
    width: 100vw;
    height: 100vh;
    margin-left: -50vw;
    top: 0px;
    left: 50%;
    will-change: opacity;

    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: opacity 0.1s ease-in-out 0s;

    .mask {
      width: 100%;
      height: 100%;
      position: fixed;
      z-index: 1000;
      background-color: rgba(0, 0, 0, 0.4);
    }

    .content {
      z-index: 2000;
      position: relative;
      width: 100%;
      background-color: rgb(255, 255, 255);
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
      max-width: 800px;
      min-width: fit-content;
      max-height: 100%;
      border-radius: 12px;
      margin: 10px;
      overflow: auto;

      .item {
        cursor: pointer;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border-radius: 12px;
        padding: 24px 16px;
        border: 1px solid #eee;

        .img {
          background-size: contain;
          width: 70px;
          height: 63px;
        }

        .name {
          font-weight: bold;
          padding: 6px 0;
          font-size: 20px;
        }

        &:nth-child(1) .img {
          background-image: url("data:image/svg+xml;base64,");
        }

        &:nth-child(2) .img {
          width: 62px;
          background-image: url("data:image/svg+xml;base64,");
        }

        &:nth-child(3) .img {
          width: 62px;
          background-image: url("data:image/svg+xml;base64,");
        }
      }
    }

    .connect-status-box {
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      z-index: 2000;
      width: 460px;
      border-radius: 10px;
      min-height: 200px;
      background: white;
    }
  }

  .myButton {
    box-shadow: 0px 1px 0px 0px #f0f7fa;
    background: linear-gradient(to bottom, #33bdef 5%, #019ad2 100%);
    background-color: #33bdef;
    border-radius: 6px;
    border: 1px solid #057fd0;
    display: inline-block;
    cursor: pointer;
    color: #ffffff;
    font-family: Arial;
    font-size: 15px;
    font-weight: bold;
    padding: 6px 24px;
    text-decoration: none;
    text-shadow: 0px -1px 0px #5b6178;
  }

  .myButton:hover {
    background: linear-gradient(to bottom, #019ad2 5%, #33bdef 100%);
    background-color: #019ad2;
  }

  .myButton:active {
    position: relative;
    top: 1px;
  }

  .connect-status {
    box-shadow: inset 0px 1px 0px 0px #ffffff;
    background: linear-gradient(to bottom, #ffffff 5%, #f6f6f6 100%);
    background-color: #ffffff;
    border-radius: 6px;
    border: 1px solid #dcdcdc;
    display: inline-block;
    cursor: pointer;
    color: #666666;
    font-family: Arial;
    font-size: 15px;
    font-weight: bold;
    padding: 6px 24px;
    text-decoration: none;
    text-shadow: 0px 1px 0px #ffffff;

    &:hover {
      background: linear-gradient(to bottom, #f6f6f6 5%, #ffffff 100%);
      background-color: #f6f6f6;
    }

    &:active {
      position: relative;
      top: 1px;
    }

  }
}
</style>
